@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*  Home page style  */

.homePageContainer {
  width: 100%;
}

.header {
  background-color: #333646;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  width: 100%;
}

.headerContent {
  width: 60%;
  display: flex;
  justify-content: center;
}

.columnOneHeader {
  color: #EBEBEC;
  width: 50%;
  text-align: start;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.nameTitle {
  font-size: 66px;
  margin: -10px 0;
  font-weight: bold;
  letter-spacing: 0.2rem;
}

.separatorHeader {
  border: none;
  height: 7px;
  width: 50px;
  background-color: #FFAF29;
  margin: 15px 0 0 0;
}

.iconContainer {
  display: flex;
  justify-content: flex-start;
  margin: 75px 0 0 0;
}

.columnTwoHeader {
  text-align: start;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
}

.introHeader {
  width: 60%;
  color: #ACADB2;
  margin: 10px 0;
}

.subtitleHeader {
  margin: 0 0 15px 0;
  color: #EBEBEC;
  width: 60%;
}

.subtitleHeaderText {
  color: #ACADB2;
  width: 60%;
}

/*  About section   */

.aboutContainer {
  background-color: #252734;
  width: 100%;
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 10vh;
}

.aboutContent {
  width: 60%;
  display: flex;
  justify-content: center;
}

.columnAboutContainer {
  display: flex;
  text-align: start;
  justify-content: center;
}

.firstColumnAbout {
  width: 50%;
}

.secColumnAbout {
  width: 50%;
}

.expProjectsContainer {
  display: flex;
  align-items: flex-end;
  height: 80px;
}

.numberExpProject {
  color: #FFAF29;
  font-size: 58px;
  margin: 0;
}

.textExpProject {
  color: #EBEBEC;
  margin: 0 30px 15px 10px;
}


/*  Projects  */

.projects {
  background-color: #333646;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 150vh;
}

.projectsContent {
  width: 60%;
}

.projectsSquareContainer {
  display: flex;
  margin-top: -130px;
  justify-content: space-between;
  width: 100%;
}

.projectsSquare {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  width: 300px;
  height: 300px;
  background-color: #424657;
  color: #EBEBEC;
  box-sizing: border-box;
  padding: 45px 15px;
  font-family: 'Open Sans', sans-serif;
  text-align: start;
}

.iconTextProjectSquare {
  display: flex;
  width: 200px;
}

.projectSquareTitle {
  font-size: 36px;
  margin: 10px 0;
}

.projectSquareTextContainer {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  margin-left: 25px;
}

.orangeSquare {
  background-color: #FFAF29;
  color: #FFF7EA;
}

/*  Selected projects */

.selectedProjects {
  margin-top: 25px;
  display: flex;
  height: 130vh;
}

.selectedProjectsContent {
  width: 100%;
  display: flex;
}

.firstColumnSelectedProjects {
  width: 50%;
  text-align: start;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.firstColumnSelectedProjects h2 {
  color: #EBEBEC;
  margin: 10px 0;
}

.firstColumnSelectedProjects p {
  color: #ACADB2;
}

.secondColumnSelectedProjects {
  width: 50%;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  justify-content: space-around;
}

.projectCard {
  background-color: #252734;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 300px;
  width: 400px;
  color: #EBEBEC;
  font-family: 'Open Sans', sans-serif;
  transition: ease-in-out 0.3s;
}
.projectCard:hover {
  transform: scale(1.8);
}

.projectCardText {
  display: flex;
  box-sizing: border-box;
  width: 100%;
  height: 20%;
  justify-content: space-around;
  align-items: center;
  text-align: start;
}

.projectCardImage {
  width: 100%;
}

.cardBeers {
  margin-top: 250px;
}

/*  Footer  */

footer {
  background-color: #252734;
  color: #EBEBEC;
  height: 25vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: start;
}

/*  Responsive  */

@media (max-width: 1060px) {
  .header {
    height: 120vh;
  }

  .headerContent {
    flex-direction: column;
  }

  .columnTwoHeader {
    width: 100%;
  }

  /*  About   */
  .aboutContainer {
    height: 120vh;
  }

  .aboutContent {
    flex-direction: column;
  }

  .columnAboutContainer {
    flex-direction: column;
  }

  .firstColumnAbout {
    width: 100%;
  }

  .secColumnAbout {
    width: 100%;
  }

  .numberExpProject {
    font-size: 42px;
  }

  .textExpProject {
    margin: 5px;
  }

  /*  Projects  */

  .projects {
    height: 300vh;
    padding: 25px;
    box-sizing: border-box;
    justify-content: space-around;
  }

  .projectsSquareContainer {
    margin: 5px;
    flex-direction: column;
  }

  .projectsSquare {
    width: 100%;
    height: 220px;
    margin: 15px 0;
  }

  .selectedProjectsContent {
    flex-direction: column;
  }

/*  Portfolio   */

  .firstColumnSelectedProjects {
    width: 100%;
  }
  .secondColumnSelectedProjects {
    width: 100%;
  }
  .projectCard {
    box-sizing: border-box;
    padding: 15px;
    width: 100%;
    height: 250px;
    margin: 15px 0;
  }

/*   Footer   */
  footer {
    flex-direction: column;
    height: 50vh;
  }

  .secondColumnFooter {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .secondColumnFooter p {
    width: 60%;
  }
}
